.link {
    border-bottom: $border;
    padding-bottom: 15px;

    &:last-child {
        border-bottom: 0; }

    @include ele(display-text) {
        min-height: 30px;
        margin-bottom: 5px; }

    @include ele(fields) {
        margin-left: 15px; }

    @include ele(text) {
        margin-bottom: 5px; } }
