          height: 25px; } }

.slide-enter-active {
  animation: slide-in .3s; }

.slide-leave-active {
  animation: slide-out .3s; }

@keyframes slide-in {
  0% {
    transform: translateX(-160px); }
  100% {
    transform: translateX(0); } }

@keyframes slide-out {
  0% {
    transform: translateX(0); }
  100% {
    transform: translateX(-160px); } }

