.project {

    @include ele(display-header) {
        margin: 0;
        color: $primaryColor; }

    @include ele(display-item) {
        margin-left: 15px;
        padding-top: 5px; }

    @include ele(display-section) {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0; } }

    @include ele(display-subsection) {
        border-bottom: $border;
        padding: 10px 0;

        &:last-child {
            border-bottom: 0; } }

    @include ele(error) {
        text-align: center; }

    @include ele(half) {
        flex: 1 0;
        width: 700px;

        &:first-child {
            padding-right: 20px;
            border-right: $border; }

        &:last-child {
            padding-left: 20px; } }

    @include ele(half-header) {
        text-align: center;
        font-style: italic;
        margin: 0 0 20px; }

    @include ele(main) {
        position: relative;
        display: flex;
        min-width: 1150px; }

    @include medium() {

        @include ele(main) {
            min-width: $widthMedium;
            width: $widthMedium;
            margin: auto; } }

    @include small() {

        @include ele(main) {
            width: $widthSmall; } }

    @include tiny() {

        @include ele(main) {
            width: 100%; } } }
